export default {
	data() {
		return {
			title: 'Hello',
			list1: '',
			userlist:''
		};
	},
	onLoad(item) {
		this.userlist = uni.getStorageSync('userlist'); //加载用户缓存
		console.log(this.userlist);
		this.judgeUserlist(item);
	},
	methods: {
		xiajidaili(item){
			console.log(item);
			if (item.isok==true) {
				this.doUrl('pages/index/index_userlist1',item);
			} else{
				uni.showToast({
					title: '当前用户暂无下级代理',
					icon: "none",
					duration: 3000
				})
			}
			
		},
		// 获取用户代理信息
		judgeUserlist: function(item) {
			this.http.getApi(
					'/user/getOneall/userid',
					// 'user/getall/userid',
					{
						userId: item.id
					},
					'get',
					this.userlist.storeOpendid).then(res => {
					console.log(res);
					console.log('获取用户信息成功');
					this.list1 = res.data.list;
					console.log(this.list1);
				}).catch(err => {
					console.log('获取用户信息失败' + JSON.stringify(err));
				});
		}
	}
};
